"use client";

import React, { useEffect, useState } from 'react';
import { LoadingSpinner, NoSSR } from '@/shared/ui';

interface ClientPageWrapperProps {
  component: () => Promise<{ default: React.ComponentType }>;
}

export const ClientPageWrapper: React.FC<ClientPageWrapperProps> = ({ component }) => {
  const [Component, setComponent] = useState<React.ComponentType | null>(null);

  useEffect(() => {
    const loadComponent = async () => {
      try {
        const componentModule = await component();
        setComponent(() => componentModule.default);
      } catch (error) {
        console.error('Failed to load component:', error);
      }
    };

    loadComponent();
  }, [component]);

  const loadingFallback = (
    <div style={{ 
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center', 
      minHeight: '50vh' 
    }}>
      <LoadingSpinner size="lg" />
    </div>
  );

  return (
    <NoSSR fallback={loadingFallback}>
      {Component ? <Component /> : loadingFallback}
    </NoSSR>
  );
};
